'use client'

import { useState, useEffect, useRef } from 'react'
import Link from 'next/link'
import { Search, Heart, ShoppingCart, User, LogOut, Moon, Sun, ChevronDown, Menu, X } from 'lucide-react'
import { useCart } from '@/context/cart-context'
import { useAuth } from '@/context/auth-context'
import { useWishlist } from '@/context/wishlist-context'
import { useTheme } from '@/context/theme-context'
import { CartDrawer } from '@/components/cart-drawer'
import { CategoryDropdown } from '@/components/category-dropdown'
import { getCategoryData } from '@/lib/category-data'

export function DualNavbar({ showCategoryBar = false }: { showCategoryBar?: boolean }) {
  const [isScrolled, setIsScrolled] = useState(false)
  const [isSearchFocused, setIsSearchFocused] = useState(false)
  const [isCartOpen, setIsCartOpen] = useState(false)
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false)
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [activeCategory, setActiveCategory] = useState<string | null>(null)
  const [searchQuery, setSearchQuery] = useState('')
  
  const { items } = useCart()
  const { items: wishlistItems } = useWishlist()
  const { user, logout } = useAuth()
  const { theme, toggleTheme } = useTheme()
  
  const userMenuRef = useRef<HTMLDivElement>(null)
  const categoryRefs = useRef<Record<string, HTMLDivElement | null>>({})

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (userMenuRef.current && !userMenuRef.current.contains(event.target as Node)) {
        setIsUserMenuOpen(false)
      }
      if (!Object.values(categoryRefs.current).some(ref => ref?.contains(event.target as Node))) {
        setActiveCategory(null)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  const mainCategories = [
    { name: 'New Arrivals', slug: 'new-arrivals' },
    { name: 'Winter Collection', slug: 'winter-collection' },
    { name: 'Deals & Offers', slug: 'deals' },
  ]

  const bottomCategories = [
    { name: 'Men', slug: 'men' },
    { name: 'Women', slug: 'women' },
    { name: 'Kids', slug: 'kids' },
    { name: 'Footwear', slug: 'footwear' },
    { name: 'Accessories', slug: 'accessories' },
    { name: 'Ethnic Wear', slug: 'ethnic-wear' },
    { name: 'Sportswear', slug: 'sportswear' },
    { name: 'Beauty & Grooming', slug: 'beauty-grooming' },
    { name: 'Home & Living', slug: 'home-living' },
    { name: 'Electronics', slug: 'electronics' },
  ]

  return (
    <>
      {/* Top Navbar */}
      <nav
        className={`fixed top-0 left-0 right-0 z-50 bg-black text-white`}
      >
        <div className="max-w-7xl mx-auto px-4 md:px-6">
          <div className="flex items-center justify-between h-20">
            {/* Logo */}
            <Link
              href="/"
              className="flex items-center gap-2 group"
            >
              <span className="text-2xl md:text-3xl font-bold tracking-tight group-hover:opacity-80 transition-opacity duration-200">
                Fashino
              </span>
            </Link>

            {/* Center: Main Categories */}
            <div className="hidden lg:flex items-center gap-8">
              {mainCategories.map((category) => (
                <Link
                  key={category.slug}
                  href={`/${category.slug}`}
                  className="text-sm font-semibold hover:opacity-80 transition-opacity duration-200 relative group"
                >
                  {category.name}
                  <span className="absolute bottom-0 left-0 w-0 h-0.5 bg-white group-hover:w-full transition-all duration-300" />
                </Link>
              ))}
            </div>

            {/* Right Section */}
            <div className="flex items-center gap-3 md:gap-4">
              {/* Search Bar */}
              <div
                className={`hidden md:flex items-center gap-2 bg-white/10 rounded-full px-4 py-2 transition-all duration-300 ${
                  isSearchFocused
                    ? 'bg-white/20 ring-2 ring-white/30'
                    : 'hover:bg-white/15'
                }`}
              >
                <Search className="w-4 h-4 text-white/80" />
                <input
                  type="text"
                  placeholder="Search for products..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  onFocus={() => setIsSearchFocused(true)}
                  onBlur={() => setIsSearchFocused(false)}
                  className="bg-transparent outline-none text-sm text-white placeholder:text-white/60 w-48 lg:w-64"
                />
              </div>
              
              {/* Mobile Search - Expandable */}
              {isSearchFocused && (
                <div className="md:hidden absolute top-full left-0 right-0 bg-black p-4 border-t border-white/10">
                  <div className="flex items-center gap-2 bg-white/10 rounded-full px-4 py-2">
                    <Search className="w-4 h-4 text-white/80" />
                    <input
                      type="text"
                      placeholder="Search for products..."
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      className="bg-transparent outline-none text-sm text-white placeholder:text-white/60 flex-1"
                      autoFocus
                    />
                    <button
                      onClick={() => setIsSearchFocused(false)}
                      className="p-1 hover:bg-white/10 rounded"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </div>
                </div>
              )}
              {!isSearchFocused && (
                <button
                  className="md:hidden p-2 hover:bg-white/10 rounded-full transition-colors duration-200"
                  onClick={() => setIsSearchFocused(true)}
                >
                  <Search className="w-5 h-5" />
                </button>
              )}

              {/* Theme Toggle */}
              <button
                onClick={toggleTheme}
                className="p-2 hover:bg-white/10 rounded-full transition-all duration-200 hover:scale-110"
                title="Toggle theme"
              >
                {theme === 'dark' ? (
                  <Sun className="w-5 h-5" />
                ) : (
                  <Moon className="w-5 h-5" />
                )}
              </button>

              {/* Wishlist */}
              <Link
                href="/wishlist"
                className="relative p-2 hover:bg-white/10 rounded-full transition-all duration-200 hover:scale-110"
                title="Wishlist"
              >
                <Heart className="w-5 h-5" />
                {wishlistItems.length > 0 && (
                  <span className="absolute -top-1 -right-1 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center font-bold">
                    {wishlistItems.length}
                  </span>
                )}
              </Link>

              {/* Cart */}
              <button
                onClick={() => setIsCartOpen(true)}
                className="relative p-2 hover:bg-white/10 rounded-full transition-all duration-200 hover:scale-110"
                title="Shopping Cart"
              >
                <ShoppingCart className="w-5 h-5" />
                {items.length > 0 && (
                  <span className="absolute -top-1 -right-1 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center font-bold">
                    {items.length}
                  </span>
                )}
              </button>

              {/* User Menu */}
              {user ? (
                <div className="relative" ref={userMenuRef}>
                  <button
                    onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                    className="flex items-center gap-2 px-3 py-2 hover:bg-white/10 rounded-full transition-all duration-200"
                  >
                    {user.profilePic ? (
                      <img
                        src={user.profilePic}
                        alt={user.name}
                        className="w-8 h-8 rounded-full object-cover ring-2 ring-white/20"
                      />
                    ) : (
                      <div className="w-8 h-8 rounded-full bg-white/20 flex items-center justify-center">
                        <User className="w-4 h-4" />
                      </div>
                    )}
                    <span className="hidden md:block text-sm font-semibold">
                      {user.name || 'User'}
                    </span>
                    <ChevronDown className={`w-4 h-4 transition-transform duration-200 ${
                      isUserMenuOpen ? 'rotate-180' : ''
                    }`} />
                  </button>

                  {/* User Dropdown */}
                  {isUserMenuOpen && (
                    <div className="absolute right-0 mt-2 w-56 bg-white dark:bg-slate-900 border border-border rounded-xl shadow-2xl overflow-hidden animate-in fade-in slide-in-from-top-2 duration-200">
                      <div className="p-4 border-b border-border">
                        <p className="font-semibold text-foreground">{user.name}</p>
                        <p className="text-sm text-muted-foreground">{user.email}</p>
                      </div>
                      <div className="py-2">
                        <Link
                          href="/dashboard"
                          className="flex items-center gap-3 px-4 py-2.5 hover:bg-muted transition-colors duration-200 text-foreground"
                          onClick={() => setIsUserMenuOpen(false)}
                        >
                          <User className="w-4 h-4 text-muted-foreground" />
                          <span className="text-sm">My Account</span>
                        </Link>
                        <Link
                          href="/wishlist"
                          className="flex items-center gap-3 px-4 py-2.5 hover:bg-muted transition-colors duration-200 text-foreground"
                          onClick={() => setIsUserMenuOpen(false)}
                        >
                          <Heart className="w-4 h-4 text-muted-foreground" />
                          <span className="text-sm">Wishlist</span>
                        </Link>
                        <button
                          onClick={() => {
                            logout()
                            setIsUserMenuOpen(false)
                          }}
                          className="w-full flex items-center gap-3 px-4 py-2.5 hover:bg-muted transition-colors duration-200 text-left text-foreground"
                        >
                          <LogOut className="w-4 h-4 text-muted-foreground" />
                          <span className="text-sm">Logout</span>
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              ) : (
                <Link
                  href="/login"
                  className="px-4 py-2 bg-white text-black rounded-full font-semibold text-sm hover:bg-white/90 transition-all duration-200 hover:scale-105"
                >
                  Sign In
                </Link>
              )}
            </div>
          </div>
        </div>
      </nav>

      {/* Bottom Navbar (Home only) */}
      {showCategoryBar && (
        <nav
          className={`relative z-40 border-b border-transparent`}
        >
        <div className="max-w-7xl mx-auto px-4 md:px-6">
          <div className="flex items-center justify-center h-16 bg-gradient-to-r from-rose-500 via-red-500 to-amber-500 text-white">
            {/* Desktop: All Categories */}
            <div className="hidden lg:flex items-center gap-6 md:gap-8">
              {bottomCategories.map((category) => {
                const categoryData = getCategoryData(category.slug)
                return (
                  <div
                    key={category.slug}
                    className="relative"
                    ref={(el) => {
                      categoryRefs.current[category.slug] = el
                    }}
                    onMouseEnter={() => {
                      if (categoryData) {
                        setActiveCategory(category.slug)
                      }
                    }}
                    onMouseLeave={() => setActiveCategory(null)}
                  >
                    <Link
                      href={`/${category.slug}`}
                      className="text-sm font-semibold text-foreground hover:text-primary transition-colors duration-200 relative py-2"
                    >
                      {category.name}
                      <span className={`absolute bottom-0 left-0 h-0.5 bg-primary transition-all duration-300 ${
                        activeCategory === category.slug ? 'w-full' : 'w-0 group-hover:w-full'
                      }`} />
                    </Link>

                    {/* Small Dropdown */}
                    {activeCategory === category.slug && categoryData && (
                      <CategoryDropdown
                        category={category}
                        data={categoryData}
                        onClose={() => setActiveCategory(null)}
                      />
                    )}
                  </div>
                )
              })}
            </div>

            {/* Mobile: Hamburger Menu */}
            <button
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className="lg:hidden p-2 hover:bg-white/20 rounded-lg transition-colors duration-[220ms] ease-out"
            >
              {isMobileMenuOpen ? (
                <X className="w-5 h-5" />
              ) : (
                <Menu className="w-5 h-5" />
              )}
            </button>
          </div>

          {/* Mobile Menu */}
          {isMobileMenuOpen && (
            <div className="lg:hidden py-4 animate-in fade-in slide-in-from-top-1 duration-[220ms] ease-out">
              <div className="space-y-2">
                {bottomCategories.map((category) => (
                  <Link
                    key={category.slug}
                    href={`/${category.slug}`}
                    className="block px-4 py-2 rounded-lg hover:bg-white/20 transition-colors duration-[220ms] ease-out font-medium"
                    onClick={() => setIsMobileMenuOpen(false)}
                  >
                    {category.name}
                  </Link>
                ))}
              </div>
            </div>
          )}
        </div>
      </nav>
      )}

      <CartDrawer isOpen={isCartOpen} onClose={() => setIsCartOpen(false)} />
    </>
  )
}

