'use client'

import Link from 'next/link'
import { ArrowRight } from 'lucide-react'
import { getProductImage } from '@/lib/utils'

interface Subcategory {
  name: string
  slug: string
  count: number
}

interface ProductPreview {
  id: number
  name: string
  price: number
  image?: string
  discount: number
}

interface CategoryData {
  subcategories: Subcategory[]
  products: ProductPreview[]
}

interface CategoryDropdownProps {
  category: { name: string; slug: string }
  data: CategoryData
  onClose: () => void
}

export function CategoryDropdown({ category, data, onClose }: CategoryDropdownProps) {
  // Show 6-8 subcategories
  const displaySubcategories = data.subcategories.slice(0, 8)
  // Show 2-4 product thumbnails
  const displayProducts = data.products.slice(0, 4)

  return (
    <div className="absolute top-full left-1/2 -translate-x-1/2 mt-2 w-80 bg-white/95 dark:bg-slate-900/95 border border-white/20 dark:border-border rounded-xl shadow-xl overflow-hidden z-50 animate-in fade-in slide-in-from-top-1 duration-[220ms] ease-out">
      <div className="p-4">
        {/* Subcategories */}
        <div className="space-y-1 mb-4">
          {displaySubcategories.map((subcategory) => (
            <Link
              key={subcategory.slug}
              href={`/${category.slug}?subcategory=${subcategory.slug}`}
              onClick={onClose}
              className="block py-2 px-3 rounded-lg hover:bg-muted/50 transition-colors duration-200 group"
            >
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium text-foreground group-hover:text-primary transition-colors duration-200">
                  {subcategory.name}
                </span>
                <span className="text-xs text-muted-foreground opacity-0 group-hover:opacity-100 transition-opacity duration-200">
                  {subcategory.count}
                </span>
              </div>
            </Link>
          ))}
        </div>

        {/* Product Thumbnails */}
        {displayProducts.length > 0 && (
          <div className="border-t border-border pt-4 mb-4">
            <div className="grid grid-cols-2 gap-3">
              {displayProducts.map((product) => {
                const imageSrc = getProductImage(product.image, category.name, undefined, product.name)
                return (
                  <Link
                    key={product.id}
                    href={`/products/${product.id}`}
                    onClick={onClose}
                    className="group"
                  >
                    <div className="relative bg-muted rounded-lg overflow-hidden hover:shadow-md transition-shadow duration-200">
                      <div className="aspect-square relative">
                        <img
                          src={imageSrc}
                          alt={product.name}
                          className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-200"
                        />
                        {product.discount > 0 && (
                          <div className="absolute top-1 left-1 bg-primary text-primary-foreground px-1.5 py-0.5 rounded text-xs font-bold">
                            -{product.discount}%
                          </div>
                        )}
                      </div>
                      <div className="p-2">
                        <p className="text-xs font-medium text-foreground line-clamp-1 group-hover:text-primary transition-colors duration-200">
                          {product.name}
                        </p>
                        <p className="text-xs font-bold text-foreground mt-0.5">
                          â‚¹{product.price.toLocaleString('en-IN')}
                        </p>
                      </div>
                    </div>
                  </Link>
                )
              })}
            </div>
          </div>
        )}

        {/* View All CTA */}
        <Link
          href={`/${category.slug}`}
          onClick={onClose}
          className="flex items-center justify-center gap-2 px-4 py-2 bg-primary text-primary-foreground rounded-lg font-semibold text-sm hover:bg-primary/90 transition-all duration-200 hover:scale-105 group"
        >
          View all
          <ArrowRight className="w-4 h-4 group-hover:translate-x-0.5 transition-transform duration-200" />
        </Link>
      </div>
    </div>
  )
}

